export default {
    name: 'HelloWorld',
    props: {
        msg: String
    },
    data() {
        return {
            time: {
                days: 0,
                hours: 0,
                minutes: 0,
                seconds: 0
            },
            images: [
                {src: require('@/assets/img/1.jpg')},
                {src: require('@/assets/img/2.jpg')},
                {src: require('@/assets/img/3.jpg')},
                {src: require('@/assets/img/4.jpg')},
            ],
        }
    },

    methods: {
        openLightbox(index) {
            this.lightboxIndex = index;
            this.showLightbox = true;
        },
        closeLightbox() {
            this.showLightbox = false;
        },
        getTimeRemaining(endtime) {
            const t = Date.parse(endtime) - Date.now();
            const seconds = Math.floor((t / 1000) % 60);
            const minutes = Math.floor((t / 1000 / 60) % 60);
            const hours = Math.floor((t / (1000 * 60 * 60)) % 24);
            const days = Math.floor(t / (1000 * 60 * 60 * 24));
            return {
                total: t,
                days: days,
                hours: hours,
                minutes: minutes,
                seconds: seconds
            };
        },
        updateClock(endtime) {
            const t = this.getTimeRemaining(endtime);

            this.time.days = t.days;
            this.time.hours = t.hours;
            this.time.minutes = t.minutes;
            this.time.seconds = t.seconds;

            if (t.total <= 0) {
                clearInterval(this.timeinterval);
            }
        },
        initializeClock(id, endtime) {
            this.updateClock(endtime);
            this.timeinterval = setInterval(() => this.updateClock(endtime), 1000);
        },

    },
    mounted() {
        const deadline = new Date('August 21, 2023 20:00:00 GMT+0000');
        this.initializeClock('countdown', deadline);
    },
    beforeDestroy() {
        clearInterval(this.timeinterval);
    }
};